import React, { useState, useEffect } from 'react';
import {
  Box,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  MenuItem,
  Paper,
  FormControl,
  InputLabel,
  Select,
  Snackbar,
  Alert,
  CircularProgress,
  IconButton,
  useMediaQuery,
  DialogContentText,
} from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';
import axios from 'axios';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';

function LeaveRequests() {
  const [requests, setRequests] = useState([]);
  const [open, setOpen] = useState(false);
  const [formData, setFormData] = useState({
    leave_type: '',
    start_date: '',
    end_date: '',
    status: 'Pending',
  });
  const [employees, setEmployees] = useState([]);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(false);
  const [loading, setLoading] = useState(false);
  const [employeeId, setEmployeeId] = useState('');
  const [editMode, setEditMode] = useState(false);
  const [selectedRequestId, setSelectedRequestId] = useState(null);
  const [deleteConfirmationOpen, setDeleteConfirmationOpen] = useState(false);
  const [requestIdToDelete, setRequestIdToDelete] = useState(null);

  const leaveTypes = [
    'Yllk zin',
    'Hastalk zni',
    'Kiisel zin',
    'Doum zni (Annelik)',
    'Doum zni (Babalk)',
  ];

  const isSmallScreen = useMediaQuery('(max-width: 600px)');

  const columns = [
    { field: 'LeaveRequestID', headerName: 'ID', width: 90 },
    { field: 'FirstName', headerName: 'Ad', flex: 1, minWidth: 100 },
    { field: 'LastName', headerName: 'Soyad', flex: 1, minWidth: 100 },
    { field: 'EmployeeID', headerName: 'alan ID', flex: 1, minWidth: 130 },
    { field: 'LeaveType', headerName: 'zin Tr', flex: 1, minWidth: 120 },
    { field: 'StartDate', headerName: 'Balang Tarihi', flex: 1, minWidth: 120 },
    { field: 'EndDate', headerName: 'Biti Tarihi', flex: 1, minWidth: 120 },
    {
      field: 'actions',
      headerName: 'lemler',
      width: isSmallScreen ? 80 : 100,
      renderCell: (params) => (
        <div>
          <IconButton aria-label="dzenle" onClick={() => handleEdit(params.row)}>
            <EditIcon />
          </IconButton>
          <IconButton aria-label="sil" onClick={() => handleDeleteConfirmation(params.row.LeaveRequestID)} color="error">
            <DeleteIcon />
          </IconButton>
        </div>
      ),
    },
  ];

  useEffect(() => {
    fetchLeaveRequests();
    fetchEmployees();
  }, []);

  const fetchLeaveRequests = async () => {
    setLoading(true);
    try {
      const response = await axios.get('http://89.252.184.134:5001/leave-requests');
      setRequests(response.data);
    } catch (error) {
      setError('zin istekleri alnrken bir hata olutu.');
    } finally {
      setLoading(false);
    }
  };

  const fetchEmployees = async () => {
    setLoading(true);
    try {
      const response = await axios.get('http://89.252.184.134:5001/employee');
      setEmployees(response.data);
    } catch (error) {
      setError('alanlar alnrken bir hata olutu.');
    } finally {
      setLoading(false);
    }
  };

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setFormData({
      leave_type: '',
      start_date: '',
      end_date: '',
      status: 'Pending',
    });
    setEmployeeId('');
    setError(null);
    setEditMode(false);
    setSelectedRequestId(null);
  };

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!employeeId || !formData.leave_type || !formData.start_date || !formData.end_date) {
      setError('Ltfen tm alanlar doldurun.');
      return;
    }
    if (new Date(formData.start_date) > new Date(formData.end_date)) {
      setError('Balang tarihi biti tarihinden sonra olamaz.');
      return;
    }
    try {
      if (editMode) {
        await axios.put(`http://89.252.184.134:5001/leave-requests/${selectedRequestId}`, {
          ...formData,
          employee_id: employeeId,
        });
      } else {
        await axios.post('http://89.252.184.134:5001/leave-requests', {
          ...formData,
          employee_id: employeeId,
        });
      }
      fetchLeaveRequests();
      handleClose();
      setSuccess(true);
    } catch (error) {
      setError('zin istei gnderilirken bir hata olutu.');
    }
  };

  const handleEdit = (row) => {
    setEditMode(true);
    setSelectedRequestId(row.LeaveRequestID);
    setEmployeeId(row.EmployeeID);
    setFormData({
      employee_id: row.EmployeeID,
      first_name: row.FirstName,
      last_name: row.LastName,
      leave_type: row.LeaveType,
      start_date: row.StartDate,
      end_date: row.EndDate,
      status: row.LeaveRequestStatus,
    });
    handleOpen();
  };

  const handleDeleteConfirmation = (id) => {
    setRequestIdToDelete(id);
    setDeleteConfirmationOpen(true);
  };

  const handleDelete = async () => {
    try {
      await axios.delete(`http://89.252.184.134:5001/leave-requests/${requestIdToDelete}`);
      fetchLeaveRequests();
      setSuccess(true);
    } catch (error) {
      setError('zin istei silinirken bir hata olutu.');
    } finally {
      setDeleteConfirmationOpen(false);
      setRequestIdToDelete(null);
    }
  };

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ mb: 2 }}>
        <Button variant="contained" onClick={handleOpen}>
          YEN ZN STE
        </Button>
      </Box>
      <Paper elevation={3} sx={{ width: '100%', overflowX: 'auto' }}>
        {loading ? (
          <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: 400 }}>
            <CircularProgress />
          </Box>
        ) : (
          <div style={{ width: '100%' }}>
            <DataGrid
              rows={requests}
              columns={columns}
              pageSize={10}
              rowsPerPageOptions={[10]}
              disableSelectionOnClick
              getRowId={(row) => row.LeaveRequestID}
              autoHeight
              sx={{
                '& .MuiDataGrid-columnHeaders': {
                  backgroundColor: (theme) => theme.palette.grey[200],
                },
              }}
            />
          </div>
        )}
      </Paper>

      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>{editMode ? 'zin steini Dzenle' : 'zin stei Gnder'}</DialogTitle>
        <DialogContent>
          <Box component="form" sx={{ mt: 1 }}>
            <FormControl fullWidth margin="normal">
              <InputLabel id="employee-id-label">alan Sein</InputLabel>
           <Select
                            labelId="employee-id-label"
                            id="employee-id"
                            value={employeeId}
                            label="alan Sein"
                            onChange={(e) => setEmployeeId(e.target.value)}
                          >
                            {employees.map((employee) => (
                              <MenuItem key={employee.employee_id} value={employee.employee_id}>
                                {employee.employee_name} ({employee.employee_id})
                              </MenuItem>
                            ))}
                          </Select>
            </FormControl>
            <FormControl fullWidth margin="normal">
              <InputLabel id="leave-type-label">zin Tr Sein</InputLabel>
              <Select
                labelId="leave-type-label"
                id="leave-type"
                name="leave_type"
                value={formData.leave_type}
                label="zin Tr Sein"
                onChange={handleChange}
              >
                {leaveTypes.map((type) => (
                  <MenuItem key={type} value={type}>
                    {type}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <TextField
              margin="normal"
              required
              fullWidth
              name="start_date"
              label="Balang Tarihi"
              type="datetime-local"
              value={formData.start_date}
              onChange={handleChange}
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="end_date"
              label="Biti Tarihi"
              type="datetime-local"
              value={formData.end_date}
              onChange={handleChange}
              InputLabelProps={{
                shrink: true,
              }}
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>ptal</Button>
          <Button onClick={handleSubmit} variant="contained">
            {editMode ? 'Gncelle' : 'Gnder'}
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog
        open={deleteConfirmationOpen}
        onClose={() => setDeleteConfirmationOpen(false)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"zin steini Sil"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            zin isteini silmek istediinizden emin misiniz?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteConfirmationOpen(false)}>ptal</Button>
          <Button onClick={handleDelete} autoFocus color='error'>
            Sil
          </Button>
        </DialogActions>
      </Dialog>
      <Snackbar open={!!error} autoHideDuration={6000} onClose={() => setError(null)}>
        <Alert onClose={() => setError(null)} severity="error" sx={{ width: '100%' }}>
          {error}
        </Alert>
      </Snackbar>
      <Snackbar open={success} autoHideDuration={6000} onClose={() => setSuccess(false)}>
        <Alert onClose={() => setSuccess(false)} severity="success" sx={{ width: '100%' }}>
          {editMode ? 'zin istei baaryla gncellendi.' : 'zin istei baaryla gnderildi.'}
        </Alert>
      </Snackbar>
    </Box>
  );
}

export default LeaveRequests;